participants.find(profile => profile.id === author)\n\n      // A profile may not be found for system messages or messages by 'me'\n      return profile || {imageUrl: '', name: ''}\n    },\n    chatImageUrl(author) {\n      return this.profile(author).imageUrl\n    },\n    authorName(author) {\n      return this.profile(author).name\n    }\n  },\n  computed: {\n    defaultChatIcon() {\n      return chatIcon\n    }\n  },\n  mounted () {\n    this._scrollDown()\n  },\n  updated () {\n    if (this.shouldScrollToBottom())\n      this.$nextTick(this._scrollDown())\n  }\n}\n</script>\n\n<style scoped>\n.sc-message-list {\n  height: 80%;\n  overflow-y: auto;\n  background-size: 100%;\n  padding: 40px 0px;\n}\n</style>\n"],"sourceRoot":""}]);

