: String,\n      default: () => ''\n    },\n    titleImageUrl: {\n      type: String,\n      default: () => ''\n    },\n    onMessageWasSent: {\n      type: Function,\n      required: true\n    },\n    messageList: {\n      type: Array,\n      default: () => []\n    },\n    newMessagesCount: {\n      type: Number,\n      default: () => 0\n    },\n    placeholder: {\n      type: String,\n      default: 'Write a reply'\n    },\n    showTypingIndicator: {\n      type: String,\n      default: () => ''\n    },\n    colors: {\n      type: Object,\n      required: false,\n      validator: c => \n        'header' in c\n        && 'bg' in c.header && 'text' in c.header\n        && 'launcher' in c\n        && 'bg' in c.launcher\n        && 'messageList' in c\n        && 'bg' in c.messageList\n        && 'sentMessage' in c\n        && 'bg' in c.sentMessage && 'text' in c.sentMessage\n        && 'receivedMessage' in c\n        && 'bg' in c.receivedMessage && 'text' in c.receivedMessage\n        && 'userInput' in c\n        && 'bg' in c.userInput && 'text' in c.userInput,\n      default: function () {\n        return {\n          header: {\n            bg: '#4e8cff',\n            text: '#ffffff'\n          },\n          launcher: {\n            bg: '#4e8cff'\n          },\n          messageList: {\n            bg: '#ffffff'\n          },\n          sentMessage: {\n            bg: '#4e8cff',\n            text: '#ffffff'\n          },\n          receivedMessage: {\n            bg: '#f4f7f9',\n            text: '#ffffff'\n          },\n          userInput: {\n            bg: '#f4f7f9',\n            text: '#565867'\n          }\n        }\n      }\n    },\n    alwaysScrollToBottom: {\n      type: Boolean,\n      default: () => false\n    },\n    messageStyling: {\n      type: Boolean,\n      default: () => false\n    }\n  },\n  computed: {\n    chatWindowTitle() {\n      if (this.title !== '') {\n        return this.title\n      }\n\n      if (this.participants.length > 1) {\n        return 'You, ' + this.participants[0].name + ' & others'\n      } else {\n        return 'You & ' + this.participants[0].name\n      }\n    }\n  },\n  components: {\n    ChatWindow\n  }\n}\n</script>\n<style scoped>\n.sc-launcher {\n  width: 60px;\n  height: 60px;\n  background-position: center;\n  background-repeat: no-repeat;\n  position: fixed;\n  right: 25px;\n  bottom: 25px;\n  border-radius: 50%;\n  box-shadow: none;\n  transition: box-shadow 0.2s ease-in-out;\n  cursor: pointer;\n}\n\n.sc-launcher:before {\n  content: '';\n  position: relative;\n  display: block;\n  width: 60px;\n  height: 60px;  \n  border-radius: 50%;\n  transition: box-shadow 0.2s ease-in-out;\n}\n\n.sc-launcher .sc-open-icon,\n.sc-launcher .sc-closed-icon {\n  width: 60px;\n  height: 60px;\n  position: fixed;\n  right: 25px;\n  bottom: 25px;\n  transition: opacity 100ms ease-in-out, transform 100ms ease-in-out;\n}\n\n.sc-launcher .sc-closed-icon {\n  transition: opacity 100ms ease-in-out, transform 100ms ease-in-out;\n  width: 60px;\n  height: 60px;\n}\n\n.sc-launcher .sc-open-icon {\n  padding: 20px;\n  box-sizing: border-box;\n  opacity: 0;\n}\n\n.sc-launcher.opened .sc-open-icon {\n  transform: rotate(-90deg);\n  opacity: 1;\n}\n\n.sc-launcher.opened .sc-closed-icon {\n  transform: rotate(-90deg);\n  opacity: 0;\n}\n\n.sc-launcher.opened:before {\n  box-shadow: 0px 0px 400px 250px rgba(148, 149, 150, 0.2);\n}\n\n.sc-launcher:hover {\n  box-shadow: 0 0px 27px 1.5px rgba(0,0,0,0.2);\n}\n\n.sc-new-messsages-count {\n  position: absolute;\n  top: -3px;\n  left: 41px;\n  display: flex;\n  justify-content: center;\n  flex-direction: column;\n  border-radius: 50%;\n\twidth: 22px;\n  height: 22px;\n  background: #ff4646;\n  color: white;\n  text-align: center;\n  margin: auto;\n  font-size: 12px;\n  font-weight: 500;\n}\n</style>\n"],"sourceRoot":""}]);

