 </form>\n  </div>\n</template>\n\n\n<script>\nimport EmojiIcon from './EmojiIcon.vue'\nimport FileIcons from './FileIcons.vue'\nimport SendIcon from './SendIcon.vue'\nimport Suggestions from './Suggestions.vue'\n\nexport default {\n  components: {\n    EmojiIcon,\n    FileIcons,\n    SendIcon,\n    Suggestions\n  },\n  props: {\n    showEmoji: {\n      type: Boolean,\n      default: () => false\n    },\n    suggestions: {\n      type: Array,\n      default: () => []\n    },\n    showFile: {\n      type: Boolean,\n      default: () => false\n    },\n    onSubmit: {\n      type: Function,\n      required: true\n    },\n    placeholder: {\n      type: String,\n      default: 'Write a reply'\n    },\n    colors: {\n      type: Object,\n      required: true\n    }\n  },\n  data () {\n    return {\n      file: null,\n      inputActive: false\n    }\n  },\n  methods: {\n    cancelFile () {\n      this.file = null\n    },\n    setInputActive (onoff) {\n      this.inputActive = onoff\n    },\n    handleKey (event) {\n      if (event.keyCode === 13 && !event.shiftKey) {\n        this._submitText(event)\n        event.preventDefault()\n      }\n    },\n    _submitSuggestion(suggestion) {\n      this.onSubmit({author: 'me', type: 'text', data: { text: suggestion }})\n    },\n    _submitText (event) {\n      const text = this.$refs.userInput.textContent\n      const file = this.file\n      if (file) {\n        if (text && text.length > 0) {\n          this.onSubmit({\n            author: 'me',\n            type: 'file',\n            data: { text, file }\n          })\n          this.file = null\n          this.$refs.userInput.innerHTML = ''\n        } else {\n          this.onSubmit({\n            author: 'me',\n            type: 'file',\n            data: { file }\n          })\n          this.file = null\n        }\n      } else {\n        if (text && text.length > 0) {\n          this.onSubmit({\n            author: 'me',\n            type: 'text',\n            data: { text }\n          })\n          this.$refs.userInput.innerHTML = ''\n        }\n      }\n    },\n    _handleEmojiPicked (emoji) {\n      this.onSubmit({\n        author: 'me',\n        type: 'emoji',\n        data: { emoji }\n      })\n    },\n    _handleFileSubmit (file) {\n      this.file = file\n    }\n  }\n}\n</script>\n\n<style>\n.sc-user-input {\n  min-height: 55px;\n  margin: 0px;\n  position: relative;\n  bottom: 0;\n  display: flex;\n  background-color: #f4f7f9;\n  border-bottom-left-radius: 10px;\n  border-bottom-right-radius: 10px;\n  transition: background-color 0.2s ease, box-shadow 0.2s ease;\n}\n\n.sc-user-input--text {\n  width: 300px;\n  resize: none;\n  border: none;\n  outline: none;\n  border-bottom-left-radius: 10px;\n  box-sizing: border-box;\n  padding: 18px;\n  font-size: 15px;\n  font-weight: 400;\n  line-height: 1.33;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  color: #565867;\n  -webkit-font-smoothing: antialiased;\n  max-height: 200px;\n  overflow: scroll;\n  bottom: 0;\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n.sc-user-input--text:empty:before {\n  content: attr(placeholder);\n  display: block; /* For Firefox */\n  /* color: rgba(86, 88, 103, 0.3); */\n  filter: contrast(15%);\n  outline: none;\n}\n\n.sc-user-input--buttons {\n  width: 100px;\n  position: absolute;\n  right: 30px;\n  height: 100%;\n  display: flex;\n}\n\n.sc-user-input--button:first-of-type {\n  width: 40px;\n}\n\n.sc-user-input--button {\n  width: 30px;\n  height: 55px;\n  margin-left: 2px;\n  margin-right: 2px;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n\n.sc-user-input.active {\n  box-shadow: none;\n  background-color: white;\n  box-shadow: 0px -5px 20px 0px rgba(150, 165, 190, 0.2);\n}\n\n.sc-user-input--button label {\n  position: relative;\n  height: 24px;\n  padding-left: 3px;\n  cursor: pointer;\n}\n\n.sc-user-input--button label:hover path {\n  fill: rgba(86, 88, 103, 1);\n}\n\n.sc-user-input--button input {\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  z-index: 99999;\n  height: 100%;\n  opacity: 0;\n  cursor: pointer;\n  overflow: hidden;\n}\n\n.file-container {\n  background-color: #f4f7f9;\n  border-top-left-radius: 10px;\n  padding: 5px 20px;\n  color: #565867;\n}\n\n.delete-file-message {\n  font-style: normal;\n  float: right;\n  cursor: pointer;\n  color: #c8cad0;\n}\n\n.delete-file-message:hover {\n  color: #5d5e6d;\n}\n\n.icon-file-message {\n  margin-right: 5px;\n}\n</style>\n"],"sourceRoot":""}]);

